import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import * as action from '../../../redux/actions/dwordDisplayAction'
class DWordDisplayFormEdit extends Component {
  handleChangeText = (e) => {
    e.preventDefault();
    this.props.actionDWordDisplayEditTextChange(e.target.value);
  };
  handleChangeWidth = (e) => {
    e.preventDefault();
    this.props.actionDWordDisplayEditWidthChange(e.target.value);
  };
  handleChangeFontSize = (e) => {
    e.preventDefault();
    this.props.actionDWordDisplayEditFontSizeChange(e.target.value);
  };
  handleChangeTag = (e) => {
    e.preventDefault();
    this.props.actionDWordDisplayEditTagChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionDWordDisplayEditLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionDWordDisplayEditTopChange(e.target.value);
  };
  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionUpdateDWordDisplay();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift = 0, offsetCtrl = 0, offestDefault = 0;
    if (event.shiftKey) {//shift
      offsetShift = 10; offestDefault = 0;
    } else { offsetShift = 0; }
    if (event.ctrlKey) {//control
      offsetCtrl = 5; offestDefault = 0;
    } else { offsetCtrl = 0; }
    if (!event.shiftKey && !event.ctrlKey) { offestDefault = 1 }
    if (event.keyCode === 37) {//Trai
      this.props.actionEditMoveLeftDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 38) {//Len
      this.props.actionEditMoveTopDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 39) {//Phai
      this.props.actionEditMoveLeftIncrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 40) {//Xuong
      this.props.actionEditMoveTopIncrease(offsetShift + offsetCtrl + offestDefault)
    }
  }
  render() {
    return (
      <Col sm="12" md="4" lg="3">
        <form autoComplete="off" key='AddLineHorizontal' id="login-form" className="form" onSubmit={this.handleSubmit} onKeyDown={this.handleKeyPress} onKeyUp={this.handleKeyUp}>
          <h3 className="text-center text-info">EDIT DWORDDISPLAY</h3>
          <button type="button" onClick=
            {this.props.actionUpdateDWordDisplay}
            className="btn btn-primary btn-block">Update</button>

          <div className="dropdown-divider"></div>

          <button type="button" onClick={this.props.actionDeleteDWordDisplay}
            className="btn btn-primary btn-block">DELETE
                </button>
          <div className="form-group">
            <small className="form-text text-muted">ID:</small>
            <input value={this.props.id} type="text" className="form-control" name="id" id="id" aria-describedby="helpIdTitle" placeholder="ID " />

            <small className="form-text text-muted">Tag:</small>
            <input value={this.props.tag} onChange={this.handleChangeTag} type="text" className="form-control" name="tag" id="tag" aria-describedby="helpIdTitle" placeholder="TAG " />

            <small className="form-text text-muted">Text:</small>
            <input value={this.props.text} onChange={this.handleChangeText} type="text" className="form-control" name="text" id="text" aria-describedby="helpIdTitle" placeholder="Text" />

            <small className="form-text text-muted">Left:</small>
            <input value={this.props.left} onChange={this.handleChangeLeft} type="text" className="form-control" name="left" id="left" aria-describedby="helpIdTitle" placeholder="Left" />

            <small className="form-text text-muted">Top:</small>
            <input value={this.props.top} onChange={this.handleChangeTop} type="text" className="form-control" name="top" id="top" aria-describedby="helpIdTitle" placeholder="Top" />

            <small className="form-text text-muted">Width:</small>
            <input value={this.props.width} onChange={this.handleChangeWidth} type="text" className="form-control" name="width" id="width" aria-describedby="helpIdTitle" placeholder="Width" />

            <small className="form-text text-muted">FontSize:</small>
            <input value={this.props.fontSize} onChange={this.handleChangeFontSize} type="text" className="form-control" name="fontSize" id="fontSize" aria-describedby="helpIdTitle" placeholder="FontSize" />
          </div>

        </form>
      </Col>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    left: state.dwordDisplay.editDWordDisplay.left,
    top: state.dwordDisplay.editDWordDisplay.top,
    fontSize: state.dwordDisplay.editDWordDisplay.fontSize,
    width: state.dwordDisplay.editDWordDisplay.width,
    tag: state.dwordDisplay.editDWordDisplay.tag,
    text: state.dwordDisplay.editDWordDisplay.text,
    id: state.dwordDisplay.editDWordDisplay.id,
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionUpdateDWordDisplay: () => {
      dispatch(action.actionUpdateDWordDisplay())
    },
    actionDeleteDWordDisplay: () => {
      dispatch(action.actionDeleteDWordDisplay())
    },
    actionDWordDisplayEditTextChange: (item) => {
      dispatch(action.actionDWordDisplayEditTextChange(item))
    },
    actionDWordDisplayEditTagChange: (item) => {
      dispatch(action.actionDWordDisplayEditTagChange(item))
    },
    actionDWordDisplayEditLeftChange: (item) => {
      dispatch(action.actionDWordDisplayEditLeftChange(item))
    },
    actionDWordDisplayEditTopChange: (item) => {
      dispatch(action.actionDWordDisplayEditTopChange(item))
    },
    actionDWordDisplayEditFontSizeChange: (item) => {
      dispatch(action.actionDWordDisplayEditFontSizeChange(item))
    },
    actionDWordDisplayEditWidthChange: (item) => {
      dispatch(action.actionDWordDisplayEditWidthChange(item))
    },
    actionEditMoveLeftDecrease: (i) => {
      dispatch(action.actionEditMoveLeftDecrease(i))
    },
    actionEditMoveLeftIncrease: (i) => {
      dispatch(action.actionEditMoveLeftIncrease(i))
    },
    actionEditMoveTopDecrease: (i) => {
      dispatch(action.actionEditMoveTopDecrease(i))
    },
    actionEditMoveTopIncrease: (i) => {
      dispatch(action.actionEditMoveTopIncrease(i))
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(DWordDisplayFormEdit)