import React, { Component } from 'react'
import { connect } from 'react-redux';
import {actionToggleBitDisplay} from '../../../redux/actions/bitDisplayAction'

class BitDisplay2 extends Component {
    handleBitDisplayClick=()=>{
        this.props.actionToggleBitDisplay({
            id:this.props.i,
            tag:this.props.tag,
            left:this.props.left,
            top:this.props.top,
            idImage:this.props.idImage,
            src:this.props.src,
            nameImage0:this.props.nameImage0,
            nameImage1:this.props.nameImage1,
            type:this.props.type,
            width:this.props.width,
        })
    }
    render() {
    const style={
        position:'absolute',
        left:parseInt(this.props.left),
        top:parseInt(this.props.top),
        width:parseInt(this.props.width),
        zIndex: 10,
    }
    const src=this.props.status===true?this.props.url1:this.props.url0
    return (
            <img onClick={this.handleBitDisplayClick} alt='' src={src} style={style}/>       )
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actionToggleBitDisplay: (item) => {
        dispatch(actionToggleBitDisplay(item))
      },
    }
}
export default connect(null,mapDispatchToProps)(BitDisplay2);