import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import arrImageBitDisplay from '../ArrayImageBitDisplay'
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from "shards-react";
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import * as action from '../../../redux/actions/bitDisplayAction'
class BitDisplayFormEdit extends Component {
  constructor(props) {
    super(props);
    this.toggle0 = this.toggle0.bind(this);
    this.toggle1 = this.toggle1.bind(this);
    this.state = { open0: false, choise0: '', open1: false, choise1: '' };
  }
  toggle0() {
    this.setState(prevState => {
      return { open0: !prevState.open0 };
    });
  }
  toggle1() {
    this.setState(prevState => {
      return { open1: !prevState.open1 };
    });
  }
  choise0(e) {
    this.setState({ choise0: e })
  }
  choise1(e) {
    this.setState({ choise1: e })
  }
  handleChangeText0 = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayEditText0Change(e.target.value);
  };
  handleChangeColor0 = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayEditColor0Change(e.target.value);
  };
  handleChangeText1 = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayEditText1Change(e.target.value);
  };
  handleChangeColor1 = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayEditColor1Change(e.target.value);
  };
  handleChangeWidth = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayEditWidthChange(e.target.value);
  };
  handleChangeFontSize = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayEditFontSizeChange(e.target.value);
  };
  handleChangeTag = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayEditTagChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayEditLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayEditTopChange(e.target.value);
  };
  handleChangeType = (e) => {
    e.preventDefault();
    this.props.actionBitDisplayEditTypeChange(e.target.value);
  };
  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionUpdateBitDisplay();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift = 0, offsetCtrl = 0, offestDefault = 0;
    if (event.shiftKey) {//shift
      offsetShift = 10; offestDefault = 0;
    } else { offsetShift = 0; }
    if (event.ctrlKey) {//control
      offsetCtrl = 5; offestDefault = 0;
    } else { offsetCtrl = 0; }
    if (!event.shiftKey && !event.ctrlKey) { offestDefault = 1 }
    if (event.keyCode === 37) {//Trai
      this.props.actionEditMoveLeftDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 38) {//Len
      this.props.actionEditMoveTopDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 39) {//Phai
      this.props.actionEditMoveLeftIncrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 40) {//Xuong
      this.props.actionEditMoveTopIncrease(offsetShift + offsetCtrl + offestDefault)
    }
  }
  GetDropDownItem0 = () => {
    return (arrImageBitDisplay.map((image, key) => {
      return (<DropdownItem key={key} onClick={() => this.props.actionBitDisplayEditNameImage0Change(image.name)}>{image.name}</DropdownItem>)
    }))
  }
  GetDropDownItem1 = () => {
    return (arrImageBitDisplay.map((image, key) => {
      return (<DropdownItem key={key} onClick={() => this.props.actionBitDisplayEditNameImage1Change(image.name)}>{image.name}</DropdownItem>)
    }))
  }
  render() {
    return (
      <Col sm="12" md="4" lg="3">
        <form autoComplete="off" key='AddLineHorizontal' id="login-form" onKeyDown={this.handleKeyPress} onKeyUp={this.handleKeyUp} className="form" onSubmit={this.handleSubmit}>
          <h3 className="text-center text-info">EDIT BITDISPLAY</h3>
          <button type="button" onClick=
            {this.props.actionUpdateBitDisplay}
            className="btn btn-primary btn-block">Update</button>

          <div className="dropdown-divider"></div>

          <button type="button" onClick={this.props.actionDeleteBitDisplay}
            className="btn btn-primary btn-block">DELETE
                </button>
          <div className="form-group">
            <div className="form-group">
              <label className="text-info">Name Image0:</label><br />
              <input type="text" placeholder="Name Image0 *" id="nameImage0" className="form-control" value={this.props.nameImage0} />
            </div>
            <div className="form-group">
              <label className="text-info">Name Image1:</label><br />
              <input type="text" placeholder="Name Image1 *" id="nameImage1" className="form-control" value={this.props.nameImage1} />
            </div>

            <small className="form-text text-muted">ID:</small>
            <input value={this.props.id} type="text" className="form-control" name="id" id="id" aria-describedby="helpIdTitle" placeholder="ID " />

            <small className="form-text text-muted">Tag:</small>
            <input value={this.props.tag} onChange={this.handleChangeTag} type="text" className="form-control" name="tag" id="tag" aria-describedby="helpIdTitle" placeholder="TAG " />

            <small className="form-text text-muted">Text0:</small>
            <input value={this.props.text0} onChange={this.handleChangeText0} type="text" className="form-control" name="text0" id="text0" aria-describedby="helpIdTitle" placeholder="Text0" />

            <small className="form-text text-muted">Color0:</small>
            <input type="color" style={{width:'100%'}} id="color0" onChange={this.handleChangeColor0} value={this.props.color0} />
            <input value={this.props.color0} onChange={this.handleChangeColor0} type="text" className="form-control" name="color0" id="color0" aria-describedby="helpIdTitle" placeholder="Color0" />

            <small className="form-text text-muted">Text1:</small>
            <input value={this.props.text1} onChange={this.handleChangeText1} type="text" className="form-control" name="text1" id="text1" aria-describedby="helpIdTitle" placeholder="Text1" />

            <small className="form-text text-muted">Color1:</small>
            <input type="color" style={{width:'100%'}} id="color1" onChange={this.handleChangeColor1} value={this.props.color1} />
            <input value={this.props.color1} onChange={this.handleChangeColor1} type="text" className="form-control" name="color1" id="color1" aria-describedby="helpIdTitle" placeholder="Color1" />

            <small className="form-text text-muted">Left:</small>
            <input value={this.props.left} onChange={this.handleChangeLeft} type="text" className="form-control" name="left" id="left" aria-describedby="helpIdTitle" placeholder="Left" />

            <small className="form-text text-muted">Top:</small>
            <input value={this.props.top} onChange={this.handleChangeTop} type="text" className="form-control" name="top" id="top" aria-describedby="helpIdTitle" placeholder="Top" />

            <small className="form-text text-muted">Width:</small>
            <input value={this.props.width} onChange={this.handleChangeWidth} type="text" className="form-control" name="width" id="width" aria-describedby="helpIdTitle" placeholder="Width" />

            <small className="form-text text-muted">FontSize:</small>
            <input value={this.props.fontSize} onChange={this.handleChangeFontSize} type="text" className="form-control" name="fontSize" id="fontSize" aria-describedby="helpIdTitle" placeholder="FontSize" />

          </div>
        </form>
      </Col>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    left: state.bitDisplay.editBitDisplay.left,
    top: state.bitDisplay.editBitDisplay.top,
    fontSize: state.bitDisplay.editBitDisplay.fontSize,
    width: state.bitDisplay.editBitDisplay.width,
    tag: state.bitDisplay.editBitDisplay.tag,
    text0: state.bitDisplay.editBitDisplay.text0,
    text1: state.bitDisplay.editBitDisplay.text1,
    color0: state.bitDisplay.editBitDisplay.color0,
    color1: state.bitDisplay.editBitDisplay.color1,
    type: state.bitDisplay.editBitDisplay.type,
    id: state.bitDisplay.editBitDisplay.id,
    nameImage0: state.bitDisplay.editBitDisplay.nameImage0,
    nameImage1: state.bitDisplay.editBitDisplay.nameImage1,
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionUpdateBitDisplay: () => {
      dispatch(action.actionUpdateBitDisplay())
    },
    actionDeleteBitDisplay: () => {
      dispatch(action.actionDeleteBitDisplay())
    },
    actionBitDisplayEditText0Change: (item) => {
      dispatch(action.actionBitDisplayEditText0Change(item))
    },
    actionBitDisplayEditText1Change: (item) => {
      dispatch(action.actionBitDisplayEditText1Change(item))
    },
    actionBitDisplayEditColor0Change: (item) => {
      dispatch(action.actionBitDisplayEditColor0Change(item))
    },
    actionBitDisplayEditColor1Change: (item) => {
      dispatch(action.actionBitDisplayEditColor1Change(item))
    },
    actionBitDisplayEditTagChange: (item) => {
      dispatch(action.actionBitDisplayEditTagChange(item))
    },
    actionBitDisplayEditLeftChange: (item) => {
      dispatch(action.actionBitDisplayEditLeftChange(item))
    },
    actionBitDisplayEditTopChange: (item) => {
      dispatch(action.actionBitDisplayEditTopChange(item))
    },
    actionBitDisplayEditFontSizeChange: (item) => {
      dispatch(action.actionBitDisplayEditFontSizeChange(item))
    },
    actionBitDisplayEditWidthChange: (item) => {
      dispatch(action.actionBitDisplayEditWidthChange(item))
    },
    actionBitDisplayEditTypeChange: (item) => {
      dispatch(action.actionBitDisplayEditTypeChange(item))
    },
    actionBitDisplayEditNameImage0Change: (item) => {
      dispatch(action.actionBitDisplayEditNameImage0Change(item))
    },
    actionBitDisplayEditNameImage1Change: (item) => {
      dispatch(action.actionBitDisplayEditNameImage1Change(item))
    },
    actionEditMoveLeftDecrease: (i) => {
      dispatch(action.actionEditMoveLeftDecrease(i))
    },
    actionEditMoveLeftIncrease: (i) => {
      dispatch(action.actionEditMoveLeftIncrease(i))
    },
    actionEditMoveTopDecrease: (i) => {
      dispatch(action.actionEditMoveTopDecrease(i))
    },
    actionEditMoveTopIncrease: (i) => {
      dispatch(action.actionEditMoveTopIncrease(i))
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(BitDisplayFormEdit)