import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import * as action from '../../../redux/actions/labelAction'
class LabelFormAdd extends Component {
  constructor(props) {
    super(props)
    this.state = {
      setColor: '',
      color: 'blue'
    }
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.actionAddLabel();
  };
  handleChangeText = (e) => {
    e.preventDefault();
    this.props.actionLabelAddTextChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionLabelAddLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionLabelAddTopChange(e.target.value);
  };
  handleChangeColor = (e) => {
    console.log(e.target.value)
    e.preventDefault();
    this.props.actionLabelAddColorChange(e.target.value);
  };
  handleChangeFontSize = (e) => {
    e.preventDefault();
    this.props.actionLabelAddFontSizeChange(e.target.value);
  };
  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionAddLine();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift = 0, offsetCtrl = 0, offestDefault = 0;
    if (event.shiftKey) {//shift
      offsetShift = 10; offestDefault = 0;
    } else { offsetShift = 0; }
    if (event.ctrlKey) {//control
      offsetCtrl = 5; offestDefault = 0;
    } else { offsetCtrl = 0; }
    if (!event.shiftKey && !event.ctrlKey) { offestDefault = 1 }
    if (event.keyCode === 37) {//Trai
      this.props.actionMoveLeftDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 38) {//Len
      this.props.actionMoveTopDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 39) {//Phai
      this.props.actionMoveLeftIncrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 40) {//Xuong
      this.props.actionMoveTopIncrease(offsetShift + offsetCtrl + offestDefault)
    }
  }

  render() {
    return (
      <Col sm="12" md="4" lg="3">
        <form autoComplete="off" key='AddLineHorizontal' id="login-form" className="form" onSubmit={this.handleSubmit} onKeyDown={this.handleKeyPress}>
          <h3 className="text-center text-info">ADD LABEL</h3>

          <div className="form-group" style={{ width: '100%' }}>
            <input style={{ width: '100%' }} type="submit" name="submit" className="btn btn-info btn-md" value="Add Data" />

            <small className="form-text text-muted">Text Label:</small>
            <input type="text" onChange={this.handleChangeText} placeholder="Name Image *" id="text" className="form-control" value={this.props.LabelText} />

            <small className="form-text text-muted">Left:</small>
            <input type="text" onChange={this.handleChangeLeft} placeholder="Left *" id="left" className="form-control" value={this.props.LabelLeft} />

            <small className="form-text text-muted">Top:</small>
            <input type="text" onChange={this.handleChangeTop} placeholder="Top *" id="top" className="form-control" value={this.props.LabelTop} />

            <small className="form-text text-muted">Color:</small>
            <input type="color" style={{width:'100%'}} id="color" onChange={this.handleChangeColor} value={this.props.LabelColor} />
            <input type="text" onChange={this.handleChangeColor} placeholder="Color *" id="color" className="form-control" value={this.props.LabelColor} />

            <small className="form-text text-muted">FontSize:</small>
            <input type="text" onChange={this.handleChangeFontSize} placeholder="FontSize *" id="fontSize" className="form-control" value={this.props.LabelFontSize} />
          </div>
        </form>
      </Col>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    LabelLeft: state.label.addLabel.left,
    LabelTop: state.label.addLabel.top,
    LabelText: state.label.addLabel.text,
    LabelColor: state.label.addLabel.color,
    LabelFontSize: state.label.addLabel.fontSize,
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionAddLabel: () => {
      dispatch(action.actionAddLabel())
    },
    actionLabelAddLeftChange: (left) => {
      dispatch(action.actionLabelAddLeftChange(left))
    },
    actionLabelAddTopChange: (top) => {
      dispatch(action.actionLabelAddTopChange(top))
    },
    actionLabelAddTextChange: (text) => {
      dispatch(action.actionLabelAddTextChange(text))
    },
    actionLabelAddColorChange: (item) => {
      dispatch(action.actionLabelAddColorChange(item))
    },
    actionLabelAddFontSizeChange: (item) => {
      dispatch(action.actionLabelAddFontSizeChange(item))
    },
    actionMoveLeftDecrease: (i) => {
      dispatch(action.actionMoveLeftDecrease(i))
    },
    actionMoveLeftIncrease: (i) => {
      dispatch(action.actionMoveLeftIncrease(i))
    },
    actionMoveTopDecrease: (i) => {
      dispatch(action.actionMoveTopDecrease(i))
    },
    actionMoveTopIncrease: (i) => {
      dispatch(action.actionMoveTopIncrease(i))
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(LabelFormAdd)