import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import * as action from '../../../redux/actions/bitAdjustAction'
class BitAdjustFormAdd extends Component {
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.actionAddBitAdjust();
  };
  handleChangeText = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustAddTextChange(e.target.value);
  };
  handleChangeWidth = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustAddWidthChange(e.target.value);
  };
  handleChangeHeight = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustAddHeightChange(e.target.value);
  };
  handleChangeFontSize = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustAddFontSizeChange(e.target.value);
  };
  handleChangeTag = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustAddTagChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustAddLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustAddTopChange(e.target.value);
  };
  handleChangeType = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustAddTypeChange(e.target.value);
  };
  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionAddWordAdjust();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift=0,offsetCtrl=0,offestDefault=0;
    if (event.shiftKey ) {//shift
      offsetShift=10;offestDefault=0;
    }else {offsetShift=0;}
    if (event.ctrlKey) {//control
      offsetCtrl=5;offestDefault=0;
    }else{offsetCtrl=0;offestDefault=0;}
    if (!event.shiftKey && !event.ctrlKey){offestDefault=1}
    if (event.keyCode === 37) {//Trai
      this.props.actionMoveLeftDecrease(offsetShift+offsetCtrl+offestDefault)
    }
    if (event.keyCode === 38) {//Len
      this.props.actionMoveTopDecrease(offsetShift+offsetCtrl+offestDefault)
    }
    if (event.keyCode === 39) {//Phai
      this.props.actionMoveLeftIncrease(offsetShift+offsetCtrl+offestDefault)
    }
    if (event.keyCode === 40) {//Xuong
      this.props.actionMoveTopIncrease(offsetShift+offsetCtrl+offestDefault)
    }
  }
  render() {
    console.log(this.props.BitAdjustText)
    return (
      <Col sm="12" md="4" lg="3">
        <form autoComplete="off" key='AddLineHorizontal' id="login-form" className="form" onSubmit={this.handleSubmit} style={{ height: '100vh', overflow: 'auto' }}>
          <h3 className="text-center text-info">ADD BITADJUST</h3>
          <div className="form-group" style={{ width: '100%' }}>
            <input style={{ width: '100%' }} type="submit" name="submit" className="btn btn-info btn-md" value="Add Data" />
          </div>
          <RadioGroup aria-label="position" name="position" value={this.props.BitAdjustType} onChange={this.handleChangeType} row>
            <FormControlLabel
              value={'1'}
              control={<Radio color="primary" />}
              label="Type1"
              labelPlacement="start"
            />
            <FormControlLabel
              value={'2'}
              control={<Radio color="primary" />}
              label="Type2"
              labelPlacement="start"
            />
            <FormControlLabel
              value={'3'}
              control={<Radio color="primary" />}
              label="Type3"
              labelPlacement="start"
            />
            <FormControlLabel
              value={'4'}
              control={<Radio color="primary" />}
              label="Type4"
              labelPlacement="start"
            />
          </RadioGroup>
          <div className="form-group">
              <small className="form-text text-muted">Tag:</small>
              <input type="text" onChange={this.handleChangeTag} onKeyDown={this.handleKeyPress} placeholder="Tag *" id="tag" className="form-control" value={this.props.BitAdjustTag} />
              
              <small className="form-text text-muted">Text:</small>
            <input type="text" onChange={this.handleChangeText} onKeyDown={this.handleKeyPress} placeholder="Text *" id="text" className="form-control" value={this.props.BitAdjustText} />

              <small className="form-text text-muted">Left:</small>
            <input type="text" onChange={this.handleChangeLeft} onKeyDown={this.handleKeyPress} placeholder="Left *" id="left" className="form-control" value={this.props.BitAdjustLeft} />
              
              <small className="form-text text-muted">Top:</small>
            <input type="text" onChange={this.handleChangeTop} onKeyDown={this.handleKeyPress} placeholder="Top *" id="top" className="form-control" value={this.props.BitAdjustTop} />
              
              <small className="form-text text-muted">Width:</small>
            <input type="text" onChange={this.handleChangeWidth} onKeyDown={this.handleKeyPress} placeholder="Width *" id="width" className="form-control" value={this.props.BitAdjustWidth} />
              
              <small className="form-text text-muted">Height:</small>
            <input type="text" onChange={this.handleChangeHeight} onKeyDown={this.handleKeyPress} placeholder="Height *" id="height" className="form-control" value={this.props.BitAdjustHeight} />
              
              <small className="form-text text-muted">Font Size:</small>
              <input type="text" onChange={this.handleChangeFontSize} onKeyDown={this.handleKeyPress} placeholder="Font Size *" id="fontSize" className="form-control" value={this.props.BitAdjustFontSize} />
          </div>
        </form>
      </Col>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    BitAdjustLeft: state.bitAdjust.addBitAdjust.left,
    BitAdjustTop: state.bitAdjust.addBitAdjust.top,
    BitAdjustFontSize: state.bitAdjust.addBitAdjust.fontSize,
    BitAdjustWidth: state.bitAdjust.addBitAdjust.width,
    BitAdjustHeight: state.bitAdjust.addBitAdjust.height,
    BitAdjustTag: state.bitAdjust.addBitAdjust.tag,
    BitAdjustText: state.bitAdjust.addBitAdjust.text,
    BitAdjustType: state.bitAdjust.addBitAdjust.type,
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionAddBitAdjust: () => {
      dispatch(action.actionAddBitAdjust())
    },
    actionBitAdjustAddTextChange: (item) => {
      dispatch(action.actionBitAdjustAddTextChange(item))
    },
    actionBitAdjustAddTagChange: (item) => {
      dispatch(action.actionBitAdjustAddTagChange(item))
    },
    actionBitAdjustAddLeftChange: (item) => {
      dispatch(action.actionBitAdjustAddLeftChange(item))
    },
    actionBitAdjustAddTopChange: (item) => {
      dispatch(action.actionBitAdjustAddTopChange(item))
    },
    actionBitAdjustAddFontSizeChange: (item) => {
      dispatch(action.actionBitAdjustAddFontSizeChange(item))
    },
    actionBitAdjustAddWidthChange: (item) => {
      dispatch(action.actionBitAdjustAddWidthChange(item))
    },
    actionBitAdjustAddHeightChange: (item) => {
      dispatch(action.actionBitAdjustAddHeightChange(item))
    },
    actionBitAdjustAddTypeChange: (item) => {
      dispatch(action.actionBitAdjustAddTypeChange(item))
    },
    actionMoveLeftDecrease: (item) => {
      dispatch(action.actionMoveLeftDecrease(item))
    },
    actionMoveLeftIncrease: (item) => {
      dispatch(action.actionMoveLeftIncrease(item))
    },
    actionMoveTopDecrease: (item) => {
      dispatch(action.actionMoveTopDecrease(item))
    },
    actionMoveTopIncrease: (item) => {
      dispatch(action.actionMoveTopIncrease(item))
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(BitAdjustFormAdd)