import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import * as action from '../../../redux/actions/labelAction'
class LabelFormEdit extends Component {

  handleChangeText = (e) => {
    e.preventDefault();
    this.props.actionLabelEditTextChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionLabelEditLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionLabelEditTopChange(e.target.value);
  };
  handleChangeColor = (e) => {
    e.preventDefault();
    this.props.actionLabelEditColorChange(e.target.value);
  };
  handleChangeFontSize = (e) => {
    e.preventDefault();
    this.props.actionLabelEditFontSizeChange(e.target.value);
  };
  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionUpdateLabel();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift = 0, offsetCtrl = 0, offestDefault = 0;
    if (event.shiftKey) {//shift
      offsetShift = 10; offestDefault = 0;
    } else { offsetShift = 0; }
    if (event.ctrlKey) {//control
      offsetCtrl = 5; offestDefault = 0;
    } else { offsetCtrl = 0; }
    if (!event.shiftKey && !event.ctrlKey) { offestDefault = 1 }
    if (event.keyCode === 37) {//Trai
      this.props.actionEditMoveLeftDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 38) {//Len
      this.props.actionEditMoveTopDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 39) {//Phai
      this.props.actionEditMoveLeftIncrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 40) {//Xuong
      this.props.actionEditMoveTopIncrease(offsetShift + offsetCtrl + offestDefault)
    }
  }
  render() {
    return (
      <Col sm="12" md="4" lg="3">
        <form autoComplete="off" key={this.props.id} onKeyDown={this.handleKeyPress} onKeyUp={this.handleKeyUp}>
          <h3 className="text-center text-info">CHANGE LABEL PARAMETER</h3>
          <button type="button" onClick=
            {this.props.actionUpdateLabel}
            className="btn btn-primary btn-block">Update</button>

          <div className="dropdown-divider"></div>

          <button type="button" onClick=
            {this.props.actionDeleteLabel}
            className="btn btn-primary btn-block">DELETE</button>
          <div className="form-group">
            <small id="id" className="form-text text-muted">ID Label</small>
            <input value={this.props.id} type="text" className="form-control" name="id" id="id" aria-describedby="helpIdTitle" placeholder="ID Label: " />

            <small id="text" className="form-text text-muted">Name Text</small>
            <input value={this.props.text} onChange={this.handleChangeText} type="text" className="form-control" name="text" id="text" aria-describedby="helpIdTitle" placeholder="Name Text: " />

            <small id="left" className="form-text text-muted">Left</small>
            <input value={this.props.left} onChange={this.handleChangeLeft} type="text" className="form-control" name="left" id="left" aria-describedby="helpIdTitle" placeholder="Left: " />

            <small id="top" className="form-text text-muted">Top</small>
            <input value={this.props.top} onChange={this.handleChangeTop} type="text" className="form-control" name="top" id="top" aria-describedby="helpIdTitle" placeholder="Top: " />

            <small id="top" className="form-text text-muted">Color</small>
            <input type="color" style={{width:'100%'}} id="color" onChange={this.handleChangeColor} value={this.props.color} />
            <input value={this.props.color} onChange={this.handleChangeColor} type="text" className="form-control" name="color" id="color" aria-describedby="helpIdTitle" placeholder="Color: " />

            <small id="top" className="form-text text-muted">FontSize</small>
            <input value={this.props.fontSize} onChange={this.handleChangeFontSize} type="text" className="form-control" name="fontSize" id="fontSize" aria-describedby="helpIdTitle" placeholder="FontSize: " />

          </div>

        </form>
      </Col>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    id: state.label.editLabel.id,
    text: state.label.editLabel.text,
    left: state.label.editLabel.left,
    top: state.label.editLabel.top,
    color: state.label.editLabel.color,
    fontSize: state.label.editLabel.fontSize,
  }
}
const mapDispatchToProps = (dispatch) => {
  return {
    actionUpdateLabel: (editItem) => {
      dispatch(action.actionUpdateLabel(editItem))
    },
    actionDeleteLabel: (id) => {
      dispatch(action.actionDeleteLabel(id))
    },
    actionLabelEditLeftChange: (left) => {
      dispatch(action.actionLabelEditLeftChange(left))
    },
    actionLabelEditTopChange: (top) => {
      dispatch(action.actionLabelEditTopChange(top))
    },
    actionLabelEditTextChange: (text) => {
      dispatch(action.actionLabelEditTextChange(text))
    },
    actionLabelEditColorChange: (item) => {
      dispatch(action.actionLabelEditColorChange(item))
    },
    actionLabelEditFontSizeChange: (item) => {
      dispatch(action.actionLabelEditFontSizeChange(item))
    },
    actionEditMoveLeftDecrease: (i) => {
      dispatch(action.actionEditMoveLeftDecrease(i))
    },
    actionEditMoveLeftIncrease: (i) => {
      dispatch(action.actionEditMoveLeftIncrease(i))
    },
    actionEditMoveTopDecrease: (i) => {
      dispatch(action.actionEditMoveTopDecrease(i))
    },
    actionEditMoveTopIncrease: (i) => {
      dispatch(action.actionEditMoveTopIncrease(i))
    },
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(LabelFormEdit);