import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import { actionUpdateImage, actionDeleteImage } from '../../../redux/actions/imageAction'
class ImageFormEdit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            id: '',
            src: '',
            left: '',
            top: '',
            nameElement: '',
            tag: '',
        }
    }
    isChange = (evt) => {
        const name = evt.target.name;
        const value = evt.target.value;
        this.setState({
            [name]: value
        });
    };

    componentDidUpdate(prevProps, prevState) {
        // only update chart if the data has changed
        if (prevProps.id !== this.props.id) {
            this.setState({
                id: this.props.id,
                src: this.props.src,
                left: this.props.left,
                top: this.props.top,
                nameElement: this.props.nameElement,
                tag: this.props.tag,
            })
        }
    }
    render() {
        return (
            <Col sm="12" md="4" lg="3">
                <h4 style={{ textAlign: "center" }}>CHANGE IMAGE PARAMETER</h4>
                <button type="button" onClick=
                    {() => this.props.actionUpdateImage({
                        id: this.state.id,
                        src: this.state.src,
                        left: this.state.left,
                        top: this.state.top,
                        nameElement: this.state.nameElement,
                        tag: this.state.tag,
                    })}
                    className="btn btn-primary btn-block">Update</button>

                <div className="dropdown-divider"></div>

                <button type="button" onClick={() => this.props.actionDeleteImage(this.state.idImage)}
                    className="btn btn-primary btn-block">DELETE
                </button>

                <form key={this.props.idImage}>
                    <div className="form-group">
                        <small id="id" className="form-text text-muted">ID Image:</small>
                        <input defaultValue={this.props.id} type="text" className="form-control" name="id" id="id" aria-describedby="helpIdTitle" placeholder="ID Image: " />

                        <small className="form-text text-muted">Tag:</small>
                        <input defaultValue={this.props.tag} onChange={(evt) => { this.isChange(evt) }} type="text" className="form-control" name="tag" id="tag" aria-describedby="helpIdTitle" placeholder="Tag " />

                        <small id="src" className="form-text text-muted">Name Image:</small>
                        <input defaultValue={this.props.src} onChange={(evt) => { this.isChange(evt) }} type="text" className="form-control" name="src" id="src" aria-describedby="helpIdTitle" placeholder="Name Image " />

                        <small id="left" className="form-text text-muted">Left:</small>
                        <input defaultValue={this.props.left} onChange={(evt) => { this.isChange(evt) }} type="text" className="form-control" name="left" id="left" aria-describedby="helpIdTitle" placeholder="Left " />

                        <small id="top" className="form-text text-muted">Top:</small>
                        <input defaultValue={this.props.top} onChange={(evt) => { this.isChange(evt) }} type="text" className="form-control" name="top" id="top" aria-describedby="helpIdTitle" placeholder="Top " />

                        <small id="nameElement" className="form-text text-muted">Name Element:</small>
                        <input defaultValue={this.props.nameElement} onChange={(evt) => { this.isChange(evt) }} type="text" className="form-control" name="nameElement" id="nameElement" aria-describedby="helpIdTitle" placeholder="Name Element " />
                    </div>
                </form>
            </Col>
        );
    }
}

const mapStateToProps = (state) => {
    console.log(state.nameElement)
    return {
        id: state.image.editImage.id,
        src: state.image.editImage.src,
        left: state.image.editImage.left,
        top: state.image.editImage.top,
        nameElement: state.image.editImage.nameElement
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        actionUpdateImage: (editItem) => {
            dispatch(actionUpdateImage(editItem))
        },
        actionDeleteImage: (id) => {
            dispatch(actionDeleteImage(id))
        }
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(ImageFormEdit);