import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import * as action from '../../../redux/actions/bitAdjustAction'
class BitAdjustFormEdit extends Component {
  handleChangeText = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustEditTextChange(e.target.value);
  };
  handleChangeWidth = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustEditWidthChange(e.target.value);
  };
  handleChangeHeight = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustEditHeightChange(e.target.value);
  };
  handleChangeFontSize = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustEditFontSizeChange(e.target.value);
  };
  handleChangeTag = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustEditTagChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustEditLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustEditTopChange(e.target.value);
  };
  handleChangeType = (e) => {
    e.preventDefault();
    this.props.actionBitAdjustEditTypeChange(e.target.value);
  };
  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionUpdateBitAdjust();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift = 0, offsetCtrl = 0, offestDefault = 0;
    if (event.shiftKey) {//shift
      offsetShift = 10; offestDefault = 0;
    } else { offsetShift = 0; }
    if (event.ctrlKey) {//control
      offsetCtrl = 5; offestDefault = 0;
    } else { offsetCtrl = 0; offestDefault = 0; }
    if (!event.shiftKey && !event.ctrlKey) { offestDefault = 1 }
    if (event.keyCode === 37) {//Trai
      this.props.actionEditMoveLeftDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 38) {//Len
      this.props.actionEditMoveTopDecrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 39) {//Phai
      this.props.actionEditMoveLeftIncrease(offsetShift + offsetCtrl + offestDefault)
    }
    if (event.keyCode === 40) {//Xuong
      this.props.actionEditMoveTopIncrease(offsetShift + offsetCtrl + offestDefault)
    }
  }
  render() {
    return (
      <Col sm="12" md="4" lg="3">
        <form autoComplete="off" className="form" onSubmit={this.handleSubmit} onKeyDown={this.handleKeyPress} onKeyUp={this.handleKeyUp}>
          <h3 className="text-center text-info">EDIT BITADJUST</h3>
          <button type="button" onClick=
            {this.props.actionUpdateBitAdjust}
            className="btn btn-primary btn-block">Update</button>

          <div className="dropdown-divider"></div>

          <button type="button" onClick={this.props.actionDeleteBitAdjust}
            className="btn btn-primary btn-block">DELETE
                </button>
          <div className="form-group">
            <small className="form-text text-muted">ID:</small>
            <input value={this.props.id} type="text" className="form-control" name="id" id="id" aria-describedby="helpIdTitle" placeholder="ID " />

            <small className="form-text text-muted">Tag:</small>
            <input value={this.props.tag} onChange={this.handleChangeTag} type="text" className="form-control" name="tag" id="tag" aria-describedby="helpIdTitle" placeholder="TAG " />

            <small className="form-text text-muted">Text:</small>
            <input value={this.props.text} onChange={this.handleChangeText} type="text" className="form-control" name="text" id="text" aria-describedby="helpIdTitle" placeholder="Text" />

            <small className="form-text text-muted">Left:</small>
            <input value={this.props.left} onChange={this.handleChangeLeft} type="text" className="form-control" name="left" id="left" aria-describedby="helpIdTitle" placeholder="Left" />

            <small className="form-text text-muted">Top:</small>
            <input value={this.props.top} onChange={this.handleChangeTop} type="text" className="form-control" name="top" id="top" aria-describedby="helpIdTitle" placeholder="Top" />

            <small className="form-text text-muted">Width:</small>
            <input value={this.props.width} onChange={this.handleChangeWidth} type="text" className="form-control" name="width" id="width" aria-describedby="helpIdTitle" placeholder="Width" />

            <small className="form-text text-muted">Height:</small>
            <input value={this.props.height} onChange={this.handleChangeHeight} type="text" className="form-control" name="height" id="height" aria-describedby="helpIdTitle" placeholder="height" />

            <small className="form-text text-muted">FontSize:</small>
            <input value={this.props.fontSize} onChange={this.handleChangeFontSize} type="text" className="form-control" name="fontSize" id="fontSize" aria-describedby="helpIdTitle" placeholder="FontSize" />

            <RadioGroup aria-label="position" name="type" value={this.props.type} onChange={this.handleChangeType} row>
              <FormControlLabel
                value={1}
                control={<Radio color="primary" />}
                label="Type1"
                labelPlacement="start"
              />
              <FormControlLabel
                value={2}
                control={<Radio color="primary" />}
                label="Type2"
                labelPlacement="start"
              />
              <FormControlLabel
                value={3}
                control={<Radio color="primary" />}
                label="Type3"
                labelPlacement="start"
              />
              <FormControlLabel
                value={4}
                control={<Radio color="primary" />}
                label="Type4"
                labelPlacement="start"
              />
            </RadioGroup>
          </div>

        </form>
      </Col>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    left: state.bitAdjust.editBitAdjust.left,
    top: state.bitAdjust.editBitAdjust.top,
    fontSize: state.bitAdjust.editBitAdjust.fontSize,
    width: state.bitAdjust.editBitAdjust.width,
    height: state.bitAdjust.editBitAdjust.height,
    tag: state.bitAdjust.editBitAdjust.tag,
    text: state.bitAdjust.editBitAdjust.text,
    id: state.bitAdjust.editBitAdjust.id,
    type: state.bitAdjust.editBitAdjust.type,
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionUpdateBitAdjust: () => {
      dispatch(action.actionUpdateBitAdjust())
    },
    actionDeleteBitAdjust: () => {
      dispatch(action.actionDeleteBitAdjust())
    },
    actionBitAdjustEditTextChange: (item) => {
      dispatch(action.actionBitAdjustEditTextChange(item))
    },
    actionBitAdjustEditTagChange: (item) => {
      dispatch(action.actionBitAdjustEditTagChange(item))
    },
    actionBitAdjustEditLeftChange: (item) => {
      dispatch(action.actionBitAdjustEditLeftChange(item))
    },
    actionBitAdjustEditTopChange: (item) => {
      dispatch(action.actionBitAdjustEditTopChange(item))
    },
    actionBitAdjustEditFontSizeChange: (item) => {
      dispatch(action.actionBitAdjustEditFontSizeChange(item))
    },
    actionBitAdjustEditWidthChange: (item) => {
      dispatch(action.actionBitAdjustEditWidthChange(item))
    },
    actionBitAdjustEditHeightChange: (item) => {
      dispatch(action.actionBitAdjustEditHeightChange(item))
    },
    actionBitAdjustEditTypeChange: (item) => {
      dispatch(action.actionBitAdjustEditTypeChange(item))
    },
    actionEditMoveLeftDecrease: (offset) => {
      dispatch(action.actionEditMoveLeftDecrease(offset))
    },
    actionEditMoveLeftIncrease: (offset) => {
      dispatch(action.actionEditMoveLeftIncrease(offset))
    },
    actionEditMoveTopDecrease: (offset) => {
      dispatch(action.actionEditMoveTopDecrease(offset))
    },
    actionEditMoveTopIncrease: (offset) => {
      dispatch(action.actionEditMoveTopIncrease(offset))
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(BitAdjustFormEdit)