import React, { Component } from 'react'
import { connect } from 'react-redux';
import {actionToggleBitDisplay} from '../../../redux/actions/bitDisplayAction'

class BitDisplay1 extends Component {
    handleBitDisplayClick=()=>{
        this.props.actionToggleBitDisplay({
            id:this.props.i,
            tag:this.props.tag,
            width:this.props.width,
            left:this.props.left,
            top:this.props.top,
            fontSize:this.props.fontSize,
            text1:this.props.text1,
            text0:this.props.text0,
            color0:this.props.color0,
            color1:this.props.color1,
            type:this.props.type,
        })
    }
    render() {
    const {classes}=this.props;
    const text=parseInt(this.props.status)===1?this.props.text1:this.props.text0;
    const color=parseInt(this.props.status)===1?this.props.color1:this.props.color0;
    const style={
        position:'absolute',
        left:parseInt(this.props.left),
        top:parseInt(this.props.top),
        padding:'1px',
        color:color,
        width:parseInt(this.props.width),
        fontSize:parseInt(this.props.fontSize),
        fontWeight:'bold',
        zIndex: 10,
    }
    return (
            <div onClick={this.handleBitDisplayClick} style={style}>
                {text}
            </div>
        )
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actionToggleBitDisplay: (item) => {
        dispatch(actionToggleBitDisplay(item))
      },
    }
}
export default connect(null,mapDispatchToProps)(BitDisplay1);